@use "../base/mixins" as m;
@use "../base/variables" as v;

.howto {
  h3 {
    text-align: center;
    text-transform: uppercase;
    color: v.$green;
    font-weight: v.$delgada;
  }
  h2 {
    text-align: center;
    margin-bottom: 4rem;
    @include m.tablet {
      font-size: 6rem;
    }
  }
}

.how-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 140rem;
  margin: 0 auto;

  @include m.tablet {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    gap: 2rem;
  }
  .square {
    border: 2px solid v.$grey;
    border-radius: 2rem;
    padding: 5rem;
    max-width: 43rem;
    min-height: 43rem;
    width: auto;
    height: auto;
    margin-bottom: 3rem;
    transition: transform 0.2s;
    @include m.tablet {
      margin-bottom: 0;
    }
    &:last-of-type {
      margin-bottom: 0;
    }
    &:hover {
      transform: scale(1.1);
    }
    & p {
      color: v.$darkgrey;
    }
    & h4 {
      padding: 2rem 0;
      font-weight: v.$bold;
      margin: 0;
      font-size: 3rem;
    }

    & .circle {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: v.$green;
      height: 7rem;
      width: 7rem;
      border-radius: 5rem;
      color: v.$white;
    }
    .circle i {
      font-size: 3rem;
      color: v.$white;
    }
  }
  .bgblack {
    background-color: v.$negro;

    & .circle {
      background-color: v.$white;
    }
    & .circle i {
      color: v.$green;
    }

    h4 {
      color: v.$white;
      font-size: 3rem;
    }
  }
  .bgwhite {
    & h4 {
      color: v.$negro;
    }
  }
}
