@use "variables" as v;

/** Media Queries **/

@mixin tablet {
  @media (min-width: #{v.$tablet}) {
    @content;
  }
}

@mixin wide {
  @media (min-width: #{v.$desktop}) {
    @content;
  }
}

@mixin grid($columnas, $rows, $espaciado) {
  display: grid;
  gap: $espaciado;
  grid-template-columns: repeat($columnas, 1fr);
  grid-template-rows: repeat($rows, 1fr);
}

@mixin btn($bgColor, $display) {
  background-color: $bgColor;
  color: v.$white;
  font-weight: v.$bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: $display;
  border: none;

  &:hover {
    background-color: darken($bgColor, 5);
    cursor: pointer;
  }
}
