@use "../base/mixins" as m;
@use "../base/variables" as v;

.webp .imagen_header {
  background-image: url("/src/img/background.webp");
}

.no-webp .imagen_header {
  background-image: url("/src/img/background.jpg");
}

.header {
  position: relative;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 110rem;
  width: auto;

  .barra {
    display: block;
    text-align: center;
    & a img {
      width: 10rem;
    }
    @include m.tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 3rem;
    }
  }
}
.languaje a {
  display: inline-block;
  color: v.$darkgreen;
  border: 2px solid v.$darkgreen;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  font-weight: 700;
  &i {
    font-size: 3rem;
  }
  &:hover {
    background-color: v.$darkgreen;
    color: v.$white;
  }
}
.navegation {
  @include m.tablet {
    display: flex;
    justify-content: space-between;
  }

  & a {
    display: block;
    text-align: center;
    color: v.$darkgrey;
    padding: 1rem;
    border-radius: 0.5rem;
    font-size: 1.8rem;

    @include m.tablet {
      margin-right: 2rem;
      font-size: 1.6rem;
    }

    &:hover {
      background-color: v.$darkgreen;
      opacity: 0.5;
      color: v.$white;
      transition: background-color 0.3s ease;
    }

    @include m.tablet {
      &:last-of-type {
        margin-right: 0;
      }
    }
  }
}

/** H1 **/

.header-contenido {
  margin-top: 6rem;
  h1 {
    font-size: 5rem;
    max-width: 100%;
  }
  @include m.tablet {
    position: absolute;
    max-width: 50%;
    top: 30%;
    left: 12%;
    h1 {
      font-size: 6rem;
    }
  }

  span {
    color: darken(v.$green, 10);
  }
  p {
    font-size: 1.8rem;
    color: v.$darkgrey;
    line-height: 1.8;
    max-width: 60rem;
  }
  & .search-container {
    max-width: 100%;
    position: relative;
    @include m.tablet {
      max-width: 60%;
    }
  }

  input[type="text"] {
    border-radius: 0.5rem;
    font-size: 1.3rem;
    border: none;
    margin-top: 5rem;
    padding: 2rem;
    opacity: 0.4;
    -webkit-box-shadow: 0px 8px 22px 13px rgba(0, 0, 0, 0.08);
    -moz-box-shadow: 0px 8px 22px 13px rgba(0, 0, 0, 0.08);
    box-shadow: 0px 8px 22px 13px rgba(0, 0, 0, 0.08);
    width: 100%;
  }
  input[type="button"] {
    position: absolute;
    right: 0;
    top: 2.5rem;

    &:hover {
      background-color: darken(v.$darkgreen, $amount: 4);
    }
  }
}

.data-grid {
  max-width: 40rem;
  margin-top: 3rem;
  @include m.grid(3, 1, 2rem);

  p {
    line-height: 1.2;
  }
}
