@use "variables" as v;
@use "mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  width: 95%;
  font-family: v.$font-2;
  font-size: 1.6rem;
  line-height: 1.4;
  padding: 0 1rem;
  @include m.tablet {
    padding: 0;
  }
}
p {
  color: v.$darkgrey;
  font-size: 1.6rem;
  margin: 0;
}
.contenedor {
  max-width: 120rem;
  margin: 0 auto;
}

a {
  color: v.$white;
  text-decoration: none;
}

h1,
h2,
h3 {
  font-family: v.$font-1;
  margin: 0%;
}

h1 {
  font-size: 4rem;
  margin-bottom: 2rem;
  max-width: 60%;
}
h2 {
  font-size: 3.4rem;
  margin-bottom: 2rem;
}
h3 {
  font-size: 2.6rem;
  text-align: center;
  text-transform: uppercase;
  color: v.$green;
  font-weight: v.$delgada;
}
h4 {
  font-size: 1.5rem;
}
