@use "../base/mixins" as m;
@use "../base/variables" as v;

.partner {
  h2 {
    font-size: 4rem;
    text-align: center;
    @include m.tablet {
      font-size: 6rem;
    }
  }
}
.partner-container {
  display: flex;
  flex-basis: 0 1 calc(50% - 1rem);
  justify-content: center;
  flex-wrap: wrap;

  @include m.tablet {
    display: flex;
    justify-content: space-evenly;
    padding-top: 3rem;
  }
}
