@use "../base/mixins" as m;
@use "../base/variables" as v;

.variety-container {
  display: flex;
  flex-direction: column-reverse;

  & .variety-info {
    span {
      color: v.$green;
    }
    p {
      color: v.$darkgrey;
    }
  }

  @include m.tablet {
    display: flex;
    flex-direction: row;
    gap: 5rem;

    & .variety-image {
      flex: 1 0 66.6%;
    }
    & .variety-info {
      flex: 1 0 33.3%;
      padding: 5rem;
    }
  }
}
.variety-image {
  margin-top: 3rem;
  text-align: center;

  @include m.tablet {
    & .greysquare {
      margin-top: 10rem;
      background-color: lighten(v.$grey, 8%);
      height: 60rem;
      width: 80rem;
      position: relative;
      z-index: 1;
    }
  }
}
.pictures {
  .pic1 img {
    width: 90%;
  }
  .pic2 img {
    width: 70%;
  }

  @include m.tablet {
    position: absolute;
    padding: 10rem 0 0 10rem;
    & .pic1,
    & .pic2 {
      position: absolute;
      z-index: 2;
    }

    & .pic1 {
      top: 0;
      margin-top: -10rem;
    }
    & .pic2 {
      bottom: 1;
      right: 1;
      margin-left: 45rem;
      margin-top: -15rem;
    }
    & .pic1 img {
      border-radius: 0.5rem;
      -webkit-box-shadow: 0px 55px 51px -32px rgba(0, 0, 0, 0.44);
      -moz-box-shadow: 0px 55px 51px -32px rgba(0, 0, 0, 0.44);
      box-shadow: 0px 55px 51px -32px rgba(0, 0, 0, 0.44);
      width: auto;
    }

    & .pic2 img {
      border-radius: 0.5rem;
      -webkit-box-shadow: 0px 55px 51px -32px rgba(0, 0, 0, 0.44);
      -moz-box-shadow: 0px 55px 51px -32px rgba(0, 0, 0, 0.44);
      box-shadow: 0px 55px 51px -32px rgba(0, 0, 0, 0.44);
      width: auto;
    }
  }
}
