@use "../base/mixins" as m;
@use "../base/variables" as v;

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;

  & .pointing-guy {
    display: hidden;
    @include m.tablet {
      visibility: unset;
      position: relative;
    }
    img {
      width: 100%;
      max-height: 100%;
      height: auto;
    }
  }
  & .contact-info {
    text-align: center;

    h2 {
      color: v.$negro;
    }

    p {
      color: v.$darkgrey;
    }

    @include m.tablet {
      text-align: left;
      position: absolute;
      max-width: 25%;
      left: 25%;

      h2 {
        color: v.$white;
      }
    }
    @include m.wide {
      left: 28%;
      max-width: 18%;
    }
  }
}
