@use "../base/mixins" as m;
@use "../base/variables" as v;

/*Generales*/
footer {
  background-color: v.$negro;
  color: v.$white;
  text-align: center;
  h4 {
    font-size: 2.4rem;
  }
  a {
    color: v.$darkgrey;
  }
  a:hover {
    color: v.$green;
  }
  span {
    color: v.$white;
  }
  @include m.tablet {
    text-align: left;
  }
}

.footer {
  padding: 4rem 8rem;
  @include m.tablet {
    h4 {
      margin: 0;
    }
  }
}

.flex-container {
  margin-bottom: 5rem;

  & .learn-more ul {
    padding: 0;
  }
  & .learn-more ul li {
    list-style: none;
  }
  @include m.tablet {
    display: flex;
    justify-content: space-between;
    & .footer-logo {
      margin-right: 8rem;
    }
  }
}

/*Learn More*/
.learn-more a {
  font-size: 1.6rem;
}

@include m.tablet {
  .learn-more ul {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .learn-more ul li {
    flex: 0 0 calc(33%);
  }
}

/*Contactus*/
.footer-Contact-us {
  & .contacto {
    display: flex;
    justify-content: center;
    @include m.tablet {
      margin-top: 1.6rem;
    }
    .align-right {
      text-align: right;
    }
  }
}

/*Social*/
.social {
  & a i {
    font-size: 2rem;
    color: v.$white;
    margin-right: 2rem;
    &:hover {
      color: v.$green;
    }
  }
  @include m.tablet {
    margin-left: 8rem;
  }
}
.social-flex {
  display: flex;
  justify-content: space-between;
  margin-top: 1.6rem;

  & a:last-of-type {
    margin-right: 0;
  }
}

.copyright {
  border-top: 1px solid v.$darkgrey;
  padding: 3rem;
  p {
    text-align: center;
  }
}
