@use "../base/mixins" as m;
@use "../base/variables" as v;

.faq {
  @include m.tablet {
    width: 70%;
    margin: 0 auto;
  }
  h2 {
    text-align: center;
    margin-bottom: 5rem;
    @include m.tablet {
      font-size: 6rem;
    }
  }
}
.questions {
  border: 2px solid v.$grey;
  border-radius: 2rem;
  margin-bottom: 3rem;
  padding: 3rem;

  @include m.tablet {
    padding: 6rem 9rem;
  }

  & .question {
    display: flex;
    justify-content: space-between;
    align-items: center;
    h2 {
      margin-bottom: 2rem;
      font-size: 2rem;
      margin: 0;
    }
    @include m.tablet {
      h2 {
        font-size: 3.4rem;
      }
    }
    i {
      font-size: 3.5rem;
    }
  }
}
.faq-text {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear,
    height 0.3s linear;
}

.hidden {
  visibility: hidden;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.show-icon {
  visibility: visible;
  opacity: 1;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear;
}

.fars {
  position: relative;
}

.far {
  position: absolute;
  top: 0;
}
